"use strict";

var React = React;
var contentsActions = contentsActions;

var BrowserFolderContentItem = React.createClass({
	displayName: "BrowserFolderContentItem",

	propTypes: {
		content: React.PropTypes.object.isRequired,
		selected: React.PropTypes.object
	},
	isSelected: function isSelected() {
		return this.props.content.id === (this.props.selected && this.props.selected.id);
	},
	onClick: function onClick() {

		contentsActions.setSelectedItem(this.props.content);
	},
	onDoubleClick: function onDoubleClick() {

		if (!this.props.content.isFile) {
			contentsActions.setSelectedFolder(this.props.content.id);
		}
	},
	render: function render() {

		var c = this.props.content,
		    title = !c.isFile ? c.path : c.link,
		    classes = ["item", !c.isFile ? 'item-folder' : 'item-file', c.isImage ? 'item-image' : '', this.isSelected() ? 'selected' : ''];

		return React.createElement(
			"div",
			{ className: classes.join(' '), title: title, onClick: this.onClick, onDoubleClick: this.onDoubleClick },
			React.createElement(
				"div",
				{ className: "item-icon" },
				React.createElement("span", null),
				React.createElement("img", { src: c.icon })
			),
			React.createElement(
				"div",
				{ className: "item-caption" },
				React.createElement(
					"span",
					null,
					c.name
				)
			)
		);
	}
});